import Divisao from "./Divisao";
import Paragrafo from "./Paragrafo";
import Titulo from "./Titulo";
import Map from "./Map";
import CardOds from "./CardOds";

function Container() {
  return (
    <div
      className="h-full p-8 flex flex-col gap-y-6 bg-no-repeat bg-cover bg-fixed"
      style={{ backgroundImage: "url(https://i.imgur.com/wXsxreG.png)" }}
    >
      <main className="flex flex-col gap-y-6">
        <Titulo titulo="Objetivos de Desenvolvimento Sustentvel" />
        <Paragrafo
          msg="Os Objetivos de Desenvolvimento Sustentvel so um apelo global
       ao para acabar com a pobreza, proteger o meio ambiente e o clima e garantir
      que as pessoas, em todos os lugares, possam desfrutar de paz e de prosperidade.
      Estes so os objetivos para os quais as Naes Unidas esto contribuindo a fim de
      que possamos atingir a Agenda 2030 no Brasil."
        />
      </main>
      <Divisao />
      <Titulo titulo="Onde Estamos?" />
      <div className="w-full h-[400px]">
        <Map />
      </div>
      <Titulo titulo="Objetivos Envolvidos no Ai Lixo:" />
      <CardOds
        background="bg-[#E5243B]"
        numero="1"
        titulo="Erradicao da pobreza"
        urlImg="https://i.imgur.com/6ErjJKo.png"
        alt="Erradicao"
        msg="O projeto de reciclagem de resduos eletrnicos pode contribuir
          para a erradicao da pobreza, proporcionando oportunidades de
          emprego em diferentes etapas do processo de reciclagem. Alm disso,
          a revenda de peas e componentes eletrnicos recuperados pode gerar
          renda para pessoas que vivem em condies de pobreza."
      />
      <CardOds
        flexDirection="flex-row-reverse"
        background="bg-[#C5192D]"
        numero="4"
        titulo="Educao de Qualidade"
        urlImg="https://i.imgur.com/uRpUhIC.png"
        alt="Educao"
        msg="O projeto pode desempenhar um papel fundamental
          na educao da populao sobre a importncia da reciclagem
          de resduos eletrnicos. Por meio do site e de programas de conscientizao,
          as pessoas podem aprender sobre o que  o e-lixo, como recicl-lo de maneira
          adequada e quais so os benefcios ambientais e sociais associados a essa prtica.
          Isso promove a educao de qualidade e a conscientizao ambiental."
      />
      <CardOds
        background="bg-[#A21942]"
        numero="8"
        titulo="Trabalho decente e crescimente econmico"
        urlImg="https://i.imgur.com/HiUdx0d.png"
        alt="Trabalho"
        msg="Ao criar empregos na coleta, desmontagem,
          reciclagem e recondicionamento de resduos eletrnicos,
          o projeto contribui para a promoo de trabalho decente 
          e o crescimento econmico. Alm disso, ao recuperar materiais
          valiosos dos resduos eletrnicos, o projeto pode gerar receita, impulsionando a economia local."
      />
      <CardOds
        flexDirection="flex-row-reverse"
        background="bg-[#FD6925]"
        numero="9"
        titulo="Indstria, inovao e infraestrutura"
        urlImg="https://i.imgur.com/N7fLdBG.png"
        alt="Educao"
        msg="O projeto promove a inovao na gesto de resduos
          eletrnicos, incentivando a criao de solues tecnolgicas
          para o tratamento e a reciclagem desses materiais. Tambm 
          estimula a infraestrutura necessria para a coleta, o processamento
          e o recondicionamento dos resduos eletrnicos."
      />
      <CardOds
        background="bg-[#FD9D24]"
        numero="11"
        titulo="Cidades e comunidades sustentveis"
        urlImg="https://i.imgur.com/PP2gIyi.png"
        alt="Cidades"
        msg="A reciclagem de resduos eletrnicos contribui
          para a criao de cidades e comunidades sustentveis,
          reduzindo a quantidade de resduos eletrnicos descartados
          em aterros sanitrios. Isso diminui os impactos ambientais
          e melhora a qualidade de vida nas comunidades locais, reduzindo
          a exposio a substncias txicas presentes em eletrnicos obsoletos."
      />
      <CardOds
        flexDirection="flex-row-reverse"
        background="bg-[#BF8B2E]"
        numero="12"
        titulo="Consumo e produo responsveis"
        urlImg="https://i.imgur.com/CBrOkCg.png"
        alt="Consumo"
        msg="Promover a reciclagem de resduos eletrnicos incentiva
          o consumo e a produo responsveis. As pessoas so incentivadas
          a prolongar a vida til de seus dispositivos eletrnicos, reutilizando
          e reciclando-os, em vez de descart-los prematuramente. Isso reduz a
          demanda por novos produtos eletrnicos e minimiza o desperdcio."
      />
      <CardOds
        background="bg-[#19486A]"
        numero="17"
        titulo="Parcerias e meios de implementao"
        urlImg="https://i.imgur.com/yujWc5c.png"
        alt="Parcerias"
        msg="O projeto de reciclagem de resduos eletrnicos
          pode estabelecer parcerias com organizaes governamentais,
          empresas privadas, organizaes no governamentais e comunidades
          locais. Essas parcerias podem viabilizar a implementao bem-sucedida 
          do projeto, reunindo recursos, conhecimento e esforos para alcanar os
          objetivos dos ODS relacionados."
      />
    </div>
  );
}

export default Container;
